<template>
    <v-container>

        <v-row justify="center">
        <IsCard
            title="افزودن محصول"
            routePath="/views/products/add"
            isCardIcon="mdi-plus"
        ></IsCard>
        </v-row>

        <v-row justify="center">
            <v-col cols="12" lg="10">
                <BaseCard heading="لیست همه محصولات">
                    <products-tbl
                        :products="products"
                        :pagination="pagination"
                        v-on:fetch-products="fetchData"
                    ></products-tbl>
                </BaseCard>
            </v-col>
        </v-row>

    </v-container>
</template>

<script>
import authHeader from "@/middleware/authHeader.js";

export default {
    name: "Products",

    components: {
        ProductsTbl: () => import("@/components/sdComponents/ProductsTbl"),
        BaseCard: () => import("@/components/commonComponents/BaseCard"),
        IsCard: () => import("@/components/commonComponents/IsCard"),
    },
    data: () => ({
        products: [],
        pagination: {},
        limit: 10,
        loaded: false,
    }),
    methods: {
        fetchData: async function (page) {
            let offset = page ? (page - 1) * this.limit : 0;
            try {
                const result = await this.$axios.post(
                    "/graphql",
                {
                    query: `query {
                        productFindAll (query: {
                            pagination: {
                                limit: ${this.limit}
                                offset: ${offset}
                            }
                        }) {
                            hits {
                                id
                                pid
                                title
                                thumbnail
                                price
                                realprice
                                description
                                amount
                                deadline_date
                                visiblity
                                disabled
                                createdAt
                                userId {
                                    id
                                    username
                                    name
                                    email
                                    phone
                                    avatar_url
                                }
                            }
                            pagination {
                                limit
                                offset
                                total
                            }
                            }
                        }`,
                    },
                        authHeader(),
                    );
                const sproducts = result.data.data.productFindAll.hits;
                this.pagination = result.data.data.productFindAll.pagination;
                this.products = sproducts
                this.loaded = true;
            } catch (error) {
                console.log(error);
            }
        },
    },
    mounted () {
        this.fetchData()
    }
}
</script>